                <el-menu-item index="/videoManager">视频管理</el-menu-item>
              </el-menu-item-group>
            </el-sub-menu>
          </el-menu>
        </el-scrollbar>
      </el-aside>
      <el-container>
        <el-main>

          <div class="main">
            <div class="main-top">
              <Breadcrumb></Breadcrumb>
            </div>
            <router-view/>
          </div>
        </el-main>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
import {mapState, mapMutations} from "vuex"
import Breadcrumb from "../components/Breadcrumb.vue";

export default {
  name: "HomeAdmin",
  data() {
    return {
      activeName: "second"
    }
  },
  components: {
    Breadcrumb
  },
  computed: {
    ...mapState(['userInfo'])
  },
  watch: {
    userInfo() {
      this.check()
    },

    $route: "check"
  },
  methods: {
    ...mapMutations(["loginOut"]),
    //验证权限
    async check() {
      //向后端发请求，验证管理员权限
      let {data} = await this.$axios({
        method: "POST",
        url: "/adminServer/check"
      })
      if (data.code) {
        await  this.$router.replace("/")
        this.$message.error("请以管理员账号登录admin页面")
      }
    },
    //退出登录
    async logout() {
      let {data} = await this.$axios({
        method: "POST",
        url: "/login/out"
      })

      if (data.code) return

      //退出登录成功,清空vuex
      this.$message.success("已退出登录")
      this.loginOut()
    },
  },

  async created() {
    await this.check()
  }
}
</script>

